@use "variables";

#intro {
    font-family: variables.$font_title;
    font-size: 2em;
    text-align: center;
    background-position: center;
}



.box_intro_desc {
    height: 85vh;
    width: 85%;
    display: table;
    position: relative;
    margin: 21px auto;
}

.box_intro {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    height: 100%;
}

.box_theme {
    background-color: variables.$purple;
    position: absolute;
    align-items: center;
    font-size: 10px;
    display: flex;
    width: 13%;
    border-radius: 63px;
    line-height: 0;
    padding: 5px;

    span {
        width: 50%;
    }

}

// ----- ----- ----- ----- ----- Switch ----- ----- ----- ----- ----- 
.switch {
    background: variables.$purple;
    border: variables.$border;
    border-radius: 1000px;
    border: none;
    position: absolute;
    cursor: pointer;
    display: flex;
    outline: none;
    top: 10px;
    left: 50px;

    img {
        width: 20px;
    }

    &::after {
        content: "";
        display: block;
        width: 30px;
        height: 30px;
        position: absolute;
        background: #F1F1F1;
        top: 0;
        left: 0;
        right: unset;
        border-radius: 100px;
        transition: .3s ease all;
        box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, .2);
    }

    &.active {
        background: variables.$purple;
        color: #000;

        &::after {
            right: 0;
            left: unset;
        }
    }

    span {
        width: 30px;
        height: 30px;
        line-height: 30px;
        display: block;
        background: none;
        color: #fff;
    }
}

.box_presentation {
    border: variables.$border;
    background-color: variables.$bg_color;
    z-index: 4;
    color: variables.$text_color;
    width: 49%;
    padding: 13px 18px;
    margin: 0 auto;
    position: relative;
    animation-name: fadeInUp;

    span {
        display: block;
    }
}

.box_welcome {
    position: relative;
    font-size: 3.8rem;
    line-height: 50px;
    color: variables.$purple;
    padding: 20px;
    border: variables.$border;
    width: 60%;
    margin: 15px auto;
    z-index: 3;
    background-color: variables.$bg_color;
    box-sizing: border-box;

    &::before {
        content: "";
        background: url('../img/cat/catEyesClosed.png') no-repeat center;
            height: 160px;
            width: 46%;
            position: absolute;
            background-size: contain;
            left: 0;
            top: -94%;
            margin: 0 auto;
            right: 0;
        
    }
}

.box_place {
    font-size: 16px;
}

.box_place span:first-child::before {
    content: "-";
    padding: 0 14px 0 8px;
    color: variables.$border_color;
}

.box_place span::after {
    content: "-";
    padding: 0 8px 0 14px;
    color: variables.$border_color;
}

.box_social_media {
    .social_media_title {
        font-size: 15px;
    }

    position: relative;
    width: 40%;
    margin: 15px auto;
    background-color: variables.$bg_color;
    border: variables.$border;
    z-index: 4;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-evenly;
    align-items: center;
    padding: 7px 0;

    ul {
        margin: 0;
        padding: 0;
        line-height: 0;
    }

    li {
        display: inline-block;
        text-decoration: none;
        list-style-type: none;
        padding: 1px;
        height: 40px;
        width: 40px;

        img {
            width: 40px;
        }
    }
}
#subMenu{
    display: inline-block;
}