@use 'variables';


@media screen and (max-width: 1024px) {

    .box_big,
    .box_litle {
        left: 0;
        right: 0;
    }
}

@media only screen and (max-width: 800px) {
    #movil_icon {
        padding: 13px;
    }

    .menu_bar {
        display: block;
        right: 0;
        width: 117px;
        background-color: variables.$purple;
        border: variables.$border;
    }
    #menu ul li ul {
        display: block;
        position: relative;
        width: 100%;
        top: 0;
    }

    .container {
        width: 653px;
    }

    #menu {
        position: absolute;
        border: variables.$border;
        background-color: variables.$bg_color;
        top: 40px;
        right: -600%;
        width: 245px;
        height: 100vh;
        padding: 20px;
        font-size: 1.2rem;

        ul {
            flex-direction: column;

            li {
                border: variables.$border;
            }
        }
    }

    .box_welcome {
        font-size: 3rem;
    }

    .box_welcome,
    .box_presentation,
    .box_social_media {
        margin: 8px auto;
    }

    .experience_info {
        width: 100%;
        border-radius: unset;
    }

    .title {
        font-size: 1.3rem;
    }

    .box_skills {
        li {
            padding: 1px 3px;
        }
    }

    .container_contact {
        width: 80% !important;
    }

    .box_code {
        ul {
            justify-content: center;
            margin: 9px auto;
        }
    }

    .scroll {
        bottom: 67px;
        background-size: 56px;
    }

    .box_intro_desc {
        width: 100%;
        margin: 69px auto;
    }
}

@media only screen and (max-width: 540px) {

    body,
    html {
        overflow: unset;
    }

    .container {
        width: 511px;
    }

    .box_welcome {
        width: 95%;
    }

    .box_social_media,
    .box_presentation {
        width: 80%;
    }

    .download_cv {
        width: 33%;
        top: 11%;
    }

    .title {
        font-size: 1.3rem;
        line-height: 38px;
    }

    .experience_info {
        text-align: center;
    }

    .scroll {
        display: none;
    }

    .col_4,
    .box_bio_desc {
        height: auto;
    }

    .container_contact {
        width: 100% !important;
    }
}

@media only screen and (max-width: 414px) {
    .col_bio_photo {
        height: 266px;
    }

    .download_cv {
        display: none !important;
    }

    .box_presentation {
        font-size: 29px;
    }

    .col_4 {
        width: 100%;
    }

    #contact,
    #other_projects,
    #featured_projects,
    #featured_projects4,
    #featured_projects3,
    #featured_projects2,
    #skills,
    #about {
        height: auto;
        margin: 100px 0;
    }

    .footer_social_media ul,
    .box_contact {
        width: 100% !important;
    }

    .box_big,
    .box_litle {
        margin-bottom: 6px;
    }

    .col_desc,
    .col_video,
    .col_bio_photo {
        width: 100%;
    }

    .title {
        padding: 20px 0;
    }

    .col_video {
        margin: 8px 0;
        margin-bottom: 100px;
    }

    .col_bio_photo {
        margin: 10px 0;
        background-position: 0px -41px;
    }

    .container {
        width: 370px;
    }

    .download_cv {
        width: 43%;
        top: 13%;
    }

    .col_left,
    .col_right {
        width: 100%;
        z-index: 0;
        float: inherit;
    }

    .col_bio {
        width: 100%;
    }

    .container_flex {
        flex-direction: column;
    }
}

@media only screen and (max-width: 320px) {
    .container {
        width: 285px !important;
    }

    .lines {
        display: none;
    }

    .box_contact,
    .col_contact_icon,
    .col_contact_frm {
        width: 100% !important;
    }

    .icon_cat {
        display: none;
    }

    .container_contact {
        width: 100% !important;
        flex-wrap: wrap;
        flex-direction: row;
    }

    .col_contact_frm {
        margin: 8px 0;
    }

    .box_place span:first-child::before,
    .box_place span:first-child::after {
        padding: 0 0px 0 0;
    }

    .box_welcome {
        width: 90%;
        font-size: 40px;
        line-height: 39px;
    }

    .box_social_media,
    .box_presentation {
        width: 90%;
    }

    .box_presentation {
        width: 78%;
        font-size: 29px;
        line-height: 30px;

        b {
            display: block;
        }
    }
}

@media only screen and (max-width: 375px) {
    .box_presentation {
        line-height: 28px;

        b {
            display: block;
        }
    }

    .box_welcome::before {

        width: 51%;

        top: -88%;

    }

    .box_tools {
        flex-direction: column;
    }

    .col_contact_icon,
    .col_contact_frm,
    .col_technologies,
    .col_code {
        width: 100% !important;
    }

    .col_contact_icon {
        display: none;
    }

    .lines {
        display: none;
    }

    .title {
        font-size: 1rem;
    }

    .box_desc {
        h2 {
            font-size: 1.5rem;
        }
    }

    .col_technologies ul {

        margin-left: 25px;
    }

    .box_tools {
        font-size: unset;
    }

    .col_contact_frm {
        margin-top: 6px;
    }

    .container_contact {
        width: 100%;

        flex-direction: row;
        flex-wrap: wrap;
    }

    .container {
        width: 341px;
    }

    .box_place span:first-child::after,
    .box_place span:first-child::before {

        padding: 0;

    }
}