@use "variables";

.box_big {
    left: -67px;
}

.box_litle {
    right: -13%;
}

.box_big,
.box_litle {
    box-sizing: border-box;
    background-color: variables.$bg_color;
    border: variables.$border;
    position: relative;
    margin-bottom: 17px;
    color: variables.$text_color;
    padding: 10px;
    z-index: 5;

    img {
        width: 19px;
        padding: 0;
        vertical-align: middle;
        margin: 0 5px;
    }

    ul {
        display: flex;
        margin: 0;
        padding: 0;
        list-style-type: none;
        justify-content: center;
        flex-wrap: wrap;

        li {
            padding: 2px;
        }
    }
}

