@use 'variables';

.col_desc,
.col_video {
    width: 47%;
    border: variables.$border;
    background-color: variables.$bg_color;
    position: relative;
    z-index: 3;
    min-height: 305px;
}

.col_desc {
    position: relative;
    float: left;
}

.col_video {
    float: right;

    iframe {
        width: 100%;
        height: 307px;
    }
}

.col_technologies,
.col_code {
    width: 49%;
    box-sizing: border-box;

    a:hover {
        background-color: variables.$purple;
    }
}

.col_technologies {
    ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        margin-left: 10px;
    }

    li {
        margin: 4px 0;
    }

    img {
        vertical-align: middle;
        margin: 0 6px;
        width: 18px !important;
    }
}

.box_tools {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    align-items: center;

    img {
        width: 20px;
    }
}

.box_tools,
.box_desc {
    padding: 19px;
    text-align: justify;
}

.col_code {
    img {
        margin: 0 auto;
    }
}

.box_desc {
    h2 {
        font-size: 2rem;
    }
}

.btn_demo,
.btn_code {
    box-sizing: border-box;
    display: block;
    margin: 10px;
    text-align: center;
    padding: 4px 20px;
    border: variables.$border;
    font-size: 15px;
}

.btn_code {
    background-color: variables.$pink;
}

.btn_demo {
    background-color: variables.$green;
}


/*- - - - - - Other projects - - - - - -*/

#projects {
    .box_project_desc {
        padding: 20px 0;

        &.h2 {
            text-align: center;
        }
    }

    li {
        background-color: variables.$bg_color;
        border: variables.$border;
        padding: 22px;
        box-sizing: border-box;
    }
}

.item {
    border: variables.$border;
    background-color: variables.$bg_color;
    padding: 30px 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.box_icon {
    background: url('../img/cat/catHeadEyes.png') no-repeat center;
    background-size: 50%;
    width: 30%;
    position: absolute;
    height: 76px;
    top: -10px;
    left: 0;
    top: 13px;
}

.box_code {
    width: 100%;
    position: relative;
    z-index: 2000;

    img {
        width: 30px !important;
        text-align: center;
    }

    ul {
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-end;
        list-style-type: none;
        margin: 0;
        padding: 0;
    }

    li {
        padding: 0 5px;
        height: 40px;
        margin: 0 2px;
        width: 40px;
    }

}

.box_skills {
    width: 100%;
    color: #919294;
    line-height: 2pxdsdd;

    ul {
        margin: 0;
        padding: 0;
        display: flex;
        font-size: 13px;
        list-style-type: none;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }

    li {
        padding: 10px;
    }
}

.box_project_desc {
    h2 {
        text-align: center;
    }

    text-align: justify;
    margin-bottom: 20px;
}

.box_desc_title {
    width: 100%;
    top: -21px;
    position: absolute;
    left: 0;
    font-size: 1.8rem;

    h2 {
        background: variables.$bg_color;
        border: variables.$border;
        width: 72%;
        font-size: 23px;
        margin: 0 auto;
        text-align: center;
        padding: 3px;
        line-height: 38px;
    }

}