@use "variables";

@font-face {
    font-family: 'Cabin Sketch';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/cabinsketch/v14/QGYpz_kZZAGCONcK2A4bGOjMnw.ttf) format('truetype');
}

@font-face {
    font-family: 'Delius';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/delius/v10/PN_xRfK0pW_9e1rtYQ.ttf) format('truetype');
}

body {
    line-height: 1.6;
    background-color: variables.$bg_color;
    color: variables.$text_color;
    font-size: 1rem;
    font-family: 'Delius', cursive;
    position: relative;
}

::selection {
    background: variables.$purple;
    color: #fff;
}

*::-webkit-input-placeholder {
    color: variables.$purple;
}

*:-moz-placeholder {
    /* FF 4-18 */
    color: variables.$purple;
}

*::-moz-placeholder {
    /* FF 19+ */
    color: variables.$purple;
}

*:-ms-input-placeholder {
    /* IE 10+ */
    color: variables.$purple;
}

h1,
h2,
h3,
h5,
h4 {
    font-family: variables.$font_title;
    font-weight: 400;
    padding: 0;
    margin: 0;
}

h1 {
    color: variables.$purple;
}

a:link {
    color: variables.$text_color;
    background-color: transparent;
    text-decoration: none;
    border-radius: 10px;
}

a:visited {
    color: variables.$text_color;
    text-decoration: none;
}

a:hover {
    color: #fff;
    text-decoration: none;
}

a:active {
    color: variables.$yellow;
    text-decoration: underline;
}

header {
    position: fixed;
    z-index: 10;
    right: 0;
}

.menu_bar {
    display: none;
    box-sizing: border-box;

    img {
        margin: -6px 0;
    }

    a {
        font-size: 1.5rem;
        color: #fff;
    }
}

#menu {
    position: relative;
    top: 2%;

    font-size: 12px;
    background-color: variables.$bg_color;
    margin: 0 auto;

    ul {
        margin: 0;
        padding: 0;
        display: flex;
        width: 100%;
        height: 100%;

        li {
            display: block;
            margin: 0px 0 4px 0;
            color: #000;
            padding: 4px 9px;
            border-bottom-left-radius: 10px;
            border-top-right-radius: 10px;
            border-left: variables.$border;
            border-top: variables.$border;
            border-bottom: variables.$border;
            background-color: variables.$bg_color;

            ul {
                display: none;
                position: absolute;
                width: 67%;
                right: 0;
                top: 26px;
                z-index: 300;
            }

            &:hover>ul {
                display: inline-block;
                

            }

            ul li {
                position: relative;
                display: block;
                background: variables.$bg_color;
                margin: 0;
                padding: 4px 9px;
                :hover{
                    color: #000;
                    
                }


            }

            ul li.active {
                background: #A96DC8;
                color: #000 !important;

            }


            a:hover {
                color: white !important;
                text-decoration: none;
                display: block;
                width: 100%;
                border-bottom-left-radius: 10px;
                border-top-left-radius: 10px;
            }
        }
    }

    &.a {
        color: variables.$text_color !important;
        text-decoration: none;
    }



}

#menu li.active {
    background: #A96DC8;
    color: #000 !important;

    &.active:hover {
        color: blue !important;
    }
}

.title {
    position: relative;
    font-size: 1.5rem;
}

.tooltip {
    position: relative;
    width: 100%;

    &:hover {
        .tooltip_text {
            visibility: visible;
            opacity: 1;
        }
    }

    .tooltip_text {
        visibility: hidden;
        width: 120px;
        background-color: variables.$purple;
        color: #fff;
        text-align: center;
        border-radius: 6px;
        padding: 6px 4px;
        font-size: 13px;
        position: absolute;
        z-index: 1;
        bottom: 125%;
        left: 50%;
        margin-left: -60px;
        opacity: 0;
        -webkit-transition: opacity 0.3s;
        transition: opacity 0.3s;
        z-index: 1000;
        line-height: 13px;

        &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: variables.$purple transparent transparent transparent;
        }
    }
}

.container {
    width: 855px;
    margin: 0 auto;
}

.container_flex {

    display: flex;
    justify-content: space-between;
}

.col_left,
.col_right {
    width: 47%;
    z-index: 4;
}

.col_left {
    float: left;
}

.col_right {
    float: right;
}

.col_4,
.box_bio_desc,
.container_bio {
    box-sizing: border-box;
}

.border {
    border: variables.$border;
}

/*- - - - - - Vertical Lines Background- - - - - -*/
.lines {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    font-size: 0;
    pointer-events: none;
    z-index: -1;
}

.lines .container_lines {
    margin-left: -712px;
    position: absolute;
    left: 50%;
    top: 0;
    width: 1424px;
}

.lines .col_line {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 20%;
    height: 100vh;

}

.lines .col_line:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 100vh;
    background: variables.$border_color;
}

.lines .col_line:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.container_flowers {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    font-size: 0;
    pointer-events: none;
    z-index: 10;

    .copyright {
        font-size: 14px;
        text-align: center;
        bottom: 0;
        position: absolute;
        margin: 0 auto;
        width: 30%;
        left: 0;
        right: 0;
    }
}

.left_icon,
.right_icon {
    position: absolute;
    width: 50%;
    background-size: 900px auto;
    height: 100vh;
    background: url('../img/bg/bgFlowers.png') no-repeat;
}

.container_flowers .left_icon {
    margin-left: -280px;
    background-position: right bottom;
}

.container_flowers .right_icon {
    margin-right: -280px;
    background-position: left bottom;
    right: 0;
}

#contact,
#other_projects,
#featured_projects,
#featured_projects4,
#featured_projects3,
#featured_projects2,
#skills,
#about,
#intro {
    width: 100%;
    height: 100vh;

}

.message {
    text-align: center;
    padding: 5px 0;
    color: #a96dc8;
    border: variables.$border;
    background-color: variables.$bg_color;
    margin: 11px auto;
    position: absolute;
    width: 87%;
    display: none;
}

#mail,
#telegram,
#github {
    border: 1px solid rgb(87 75 93 / 0%);

    &:hover {
        transition: 2ms;
        -webkit-transition: 2ms;
        border: variables.$border;
        background-color: variables.$purple !important;
        border-radius: 24px;
    }
}

#github {
    background: url('../img/icon/social_media/github_intro.png') no-repeat;
    background-size: 81%;
    background-position: center;

    &:hover {
        background: url('../img/icon/social_media/cat_intro_hover.png') no-repeat;
        background-size: 81%;
        background-position: center;
    }
}

#telegram {
    background: url('../img/icon/social_media/telegram.png') no-repeat;
    background-size: 81%;
    background-position: center;

    &:hover {
        background: url('../img/icon/social_media/telegram_hover.png') no-repeat;
        background-size: 81%;
        background-position: center;
    }
}

#mail {
    background: url('../img/icon/social_media/mail.png') no-repeat;
    background-size: 81%;
    background-position: center;

    &:hover {
        background: url('../img/icon/social_media/mail_hover.png') no-repeat;
        background-size: 81%;
        background-position: center;
    }
}

#github_mini,
#demo {
    height: 30px;
    width: 30px;
    background-position: center;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 24px;
    border: variables.$border;

    &:hover {
        height: 30px;
        width: 30px;
        transition: 2ms;
        background-position: center;
        padding: 20px;
        border-radius: 24px;
        border: variables.$border;
        background-color: variables.$purple !important;
    }
}

#github_mini {
    background: url('../img/icon/social_media/github_intro.png') no-repeat;
    background-size: 62%;
    background-position: center;

    &:hover {
        background: url('../img//icon/social_media/cat_intro_hover.png') no-repeat;
        background-size: 62%;
        background-position: center;
    }
}

#demo {
    background: url('../img//icon/social_media/demo.png') no-repeat;
    background-size: 62%;
    background-position: center;

    &:hover {
        background: url('../img//icon/social_media/demo_hover.png') no-repeat;
        background-size: 62%;
        background-position: center;
    }
}