@use 'variables';

.container_flex_bio{
    display: flex;
}
.experience_info {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    background: variables.$bg_color;
    width: 65%;
    padding: 0 4px;
    border-radius: 6px;
}

.experience_number {
    display: inline-block;
    font-size: 6rem;
    line-height: 1;
    font-family: 'Karla', sans-serif;
    color: variables.$purple;
}

.experience_text {
    display: inline-block;
    color: variables.$text_color;
    line-height: 1.4;
    font-weight: 700;
}

.container_bio {
    width: 100%;
}

.col_bio_photo {
    border: variables.$border;
    background: url('../img/perfil.jpg') no-repeat;
    background-size: cover;
    background-position: center;
    box-sizing: border-box;
}

.col_bio_photo {
    position: relative;
    width: 29.5%;
    float: left;
    height: 100%;
}

.col_bio {
    width: 69.5%;
    float: right;
}

.box_bio_desc {
    padding: 10px;
}

.box_bio_desc {
    border-top: variables.$border;
    border-left: variables.$border;
    border-right: variables.$border;
    height: 252px;
    text-align: justify;
    span{
        text-align: center;
        display: block;
        padding: 20px 0;    
        color: variables.$purple;
    }

}

.col_4 {
    position: relative;
    width: 33.3333%;
    font-size: 12px;
    box-sizing: border-box;
    padding: 13px 9px;
    border: variables.$border;
    overflow-y: overlay;
    

    ul{
        margin: 0;
        padding: 0;
        display: flex;
        text-decoration: none;
        list-style-type: none;
        flex-wrap: wrap;
        -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
        justify-content: center;
        flex-direction: row;

        li{
            margin: 7px 2px;
            border-radius: 8px;
            padding: 10px;
            box-sizing: border-box;
        }
    }
}

.col_bio_photo,
.box_bio_desc,
.col_4 {
    position: relative;
    background-color: variables.$bg_color;
}

#music,
#fashion,
#cine,
#plants,
#pets{
    background-size: 100%  !important;
    height: 30px;
    width: 30px;
    text-decoration: none;
    background-position: center !important;
}
#music{
    background: url('../img/icon/interests/music.png') no-repeat;      
}
#plants{
    background: url('../img/icon/interests/plants.png') no-repeat;    
}
#cine{
    background: url('../img/icon/interests/cine.png') no-repeat;    
}
#fashion{
    background: url('../img/icon/interests/fashion.png') no-repeat;    
}
#pets{
    background: url('../img/icon/interests/pets.png') no-repeat;
}