@use 'variables';

.container_contact {
    width: 60%;
    margin: 0 auto;
    display: flex;
    background-color: variables.$bg_color;
    box-sizing: border-box;
}

.box_contact {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 17px;

    .footer_social_media {
        padding-top: 20px;

        ul {
            margin: 0;
            padding: 0;
            list-style-type: none;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: space-evenly;
            width: 33%;
            margin: 0 auto;
            flex-wrap: wrap;
            align-content: flex-start;
            justify-content: center;

            li {
                display: inline-block;
                text-decoration: none;
                list-style-type: none;
                padding: 1px;
                height: 40px;
                width: 40px;
                img {
                    width: 40px;
                }
            }
        }

        img {
            width: 40px;
        }
    }
}

.col_contact_frm {
    border: variables.$border;
    padding: 20px;
}

.col_contact_icon {
    border-top: variables.$border;
    padding: 20px;
    border-bottom: variables.$border;
    border-left: variables.$border;
    box-sizing: border-box;
    position: relative;
}

.col_contact_icon {
    width: 50%;
    position: relative;

}

.col_contact_frm {
    width: 60%;
    position: relative;

    textarea,
    input,
    input[type="text"] {
        border: variables.$border;
        background-color: variables.$bg_color;
        color: variables.$purple;
        font-family: variables.$font_desc;
        font-size: 14px;
        display: block;
        width: 100%;
        margin: 4px 0;
        padding: 10px;
    }

    textarea {
        height: 100px;
    }

    input {
        height: 34px;
        border-radius: 6px;

    }

    input[type="submit"] {
        width: 100%;
        height: 48px;
        font-weight: bold;
        background-color: #423451;
        border: variables.$border;
        color: variables.$text_color;

        &:hover{
            background-color: variables.$purple;
            color: #fff;
        }
    }
}