@use "variables";

/*- - - - - - Animation- - - - - -*/
.scroll {
    background: transparent url('../img/bg/moon.png') no-repeat;
    width: 100%;
    height: 159px;
    bottom: 20px;
    background-position-x: center;
    background-position-y: bottom;
    background-size: 97px;
    background-repeat: no-repeat;
}

.download_cv {
    background: transparent url('../img/bg/download_cv.png') no-repeat;
    z-index: 4;
    width: 15%;
    height: 138px;
    top: 14%;
    line-height: 1.1;
    right: 0;
    background-position-x: center;
    background-size: contain;
    background-repeat: no-repeat;
    text-decoration: none;
    font-size: 1.2rem;
}

.icon_cat,
.download_cv,
.scroll {
    -webkit-animation: animation 3s infinite ease both;
    animation: animation 3s infinite ease both;
    position: absolute;
}

@-ms-keyframes animation {
    50% {
        -ms-transform: translate(0, 10px);
    }

    100% {
        -ms-transform: translate(0, 0);
    }
}

@keyframes animation {
    50% {
        -webkit-transform: translate(0, 10px);
    }

    100% {
        -webkit-transform: translate(0, 0);
    }
}

.icon_cat {
    background: url('../img/cat/catCup.png');
    width: 100%;
    z-index: 4;
    height: 100%;
    background-size: contain;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background-repeat: no-repeat;
    background-position: center;
    left: 0;
    top: 0;
}

@-moz-keyframes spin {
    100% {
        -moz-transform: rotate(360deg);
    }
}

@-webkit-keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}